.mosu-logo {
	font-family: 'fira-sans-2', sans-serif;
	font-weight: bold;
}


// Utilities & Functions
@function generateRandomColor() {
	$color1: #556270;
	$color2: #4ECDC4;
	$color3: #C7F464;
	$color4: #FF6B6B;
	$color5: #C44D58;
	$colors: $color1,$color2,$color3,$color4,$color5;
	$key: random( length($colors) );
	$nth: nth( $colors, $key );
	$card-bgc: $nth !default;
	@return $card-bgc;
}